<template>
  <div class="TodayRemindEvent">
    <div class="header">
      <div class="left">
        <div class="h-cel title">今日提醒事件</div>
        <div class="h-cel">
          <SelectShop v-model="searchData.sid" @on-select="getList" />
        </div>
      </div>
      <div class="right">
        <div class="bt">查看详情 ></div>
      </div>
    </div>
    <div class="content">
      <vxe-table
        class="table-style"
        :data="tableData"
        :header-cell-class-name="headerCellClassName"
        :height="326"
        :loading="loading"
        :row-config="{ isHover: true }"
        size="mini"
      >
        <vxe-column field="time" title="提醒时间" width="140" />
        <vxe-column
          field="info"
          show-overflow="title"
          title="提醒内容"
          width="200"
        />
        <vxe-column field="staff_name" title="提醒人员" />
        <vxe-column field="vip_name" title="会员信息">
          <template #default="{ row }">
            <div>{{ row.vip_name }}</div>
            <div>{{ row.tel }}</div>
          </template>
        </vxe-column>
        <vxe-column field="shop_name" title="所属门店" />
        <vxe-column field="status_name" title="状态" />
      </vxe-table>
    </div>
  </div>
</template>
<script>
  import SelectShop from '@/components/form/select-shop'
  import { getWarningList } from '@/api/vip'

  export default {
    name: 'TodayRemindEvent',
    components: {
      SelectShop,
    },
    data: function () {
      return {
        loading: false,
        tableData: [],
        searchData: {
          sid: '',
          p: 1,
          page: 5,
          search: '', //提醒内容或员工名称
          status: '', //1未执行 2已执行 3取消执行
          start: '', //起始时间
          end: '', //结束时间
        },
      }
    },
    activated() {
      this.getList()
    },
    created() {
      this.getList()
    },
    methods: {
      headerCellClassName() {
        return 'col-color'
      },
      async getList() {
        const toDay = this.getFormatDate(new Date()).substr(0, 11)
        const start = toDay + '00:00:00'
        const end = toDay + '23:59:59'
        this.searchData.start = start
        this.searchData.end = end
        this.loading = true
        const { data } = await getWarningList(this.searchData)
        this.loading = false
        this.tableData = data.list
      },
      getFormatDate(date) {
        let month = date.getMonth() + 1
        let strDate = date.getDate()
        if (month >= 1 && month <= 9) {
          month = '0' + month
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = '0' + strDate
        }
        return (
          date.getFullYear() +
          '-' +
          month +
          '-' +
          strDate +
          ' ' +
          date.getHours() +
          ':' +
          date.getMinutes() +
          ':' +
          date.getSeconds()
        )
      },
    },
  }
</script>
